import { useState } from 'react';
import clsx from 'clsx';

import styles from './LimitSelf.module.scss';

function LimitSelf() {

	const [isLimitSelf, setIsLimitSelf] = useState(false);

	return (
		
		<div className={styles.limitSelf}>
			<div className={styles.hand}></div>
			<div className={styles.info}>
				<div className={styles.infoText}>
					            ,      .          .
				</div>
				<label className={clsx(
					styles.infoLabel,
					{ [styles.infoLabelActive] : isLimitSelf},
				)}>
					<input className={styles.infoCheckbox} type="checkbox" checked={isLimitSelf} onChange={() => setIsLimitSelf((check) => !check)} />  
				</label>
				<div className={styles.infoName}>  </div>
				<div className={styles.infoSelect}>  </div>
				<button className={styles.btn}></button>
			</div>
		</div>
				
	);
}

export default LimitSelf;