import { useState } from 'react';
import clsx from 'clsx';

import styles from './LimitSession.module.scss';

function LimitSession() {

	const [isDaylyLimit, setIsDaylyLimit] = useState(false);
	const [isWeeklyLimit, setIsWeeklyLimit] = useState(false);
	const [isMonthlyLimit, setIsMonthlyLimit] = useState(false);

	return (
		
		<div className={styles.limitSession}>
			<div className={styles.title}> </div>
			<div className={styles.info}> </div>
			<div className={styles.titleTwo}> </div>
			<ul className={styles.list}>
				<li className={styles.item}>
					<div className={styles.itemLeft}>
						<label className={clsx(
							styles.itemLabel,
							{ [styles.itemLabelActive] : isDaylyLimit},
						)}>
							<input 
								className={styles.itemCheckbox} 
								type="checkbox" 
								checked={isDaylyLimit} 
								onChange={() => setIsDaylyLimit((dayly) => !dayly)} 
							/>
							 
						</label>
						<div className={styles.itemText}> </div>
					</div>
					<div className={styles.itemRight}>
						<div className={styles.itemLine}></div>
						<ul className={styles.itemLineSublist}>
							<li className={styles.itemLineItem}>
								<button className={clsx(styles.itemLineItemBtn, styles.itemLineItemBtnActive)}>0 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>6 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>12 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>18 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>24 .</button>
							</li>
						</ul>
					</div>
				</li>
				<li className={styles.item}>
					<div className={styles.itemLeft}>
						<label className={clsx(
							styles.itemLabel,
							{ [styles.itemLabelActive] : isWeeklyLimit},
						)}>
							<input 
								className={styles.itemCheckbox} 
								type="checkbox" 
								checked={isWeeklyLimit} 
								onChange={() => setIsWeeklyLimit((weekly) => !weekly)} 
							/>
							 
						</label>
						<div className={styles.itemText}> </div>
					</div>
					<div className={styles.itemRight}>
						<div className={styles.itemLine}></div>
						<ul className={styles.itemLineSublist}>
							<li className={styles.itemLineItem}>
								<button className={clsx(styles.itemLineItemBtn, styles.itemLineItemBtnActive)}>0 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>1 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>2 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>3 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>4 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>5 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>6 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>7 .</button>
							</li>
						</ul>
					</div>
				</li>
				<li className={styles.item}>
					<div className={styles.itemLeft}>
						<label className={clsx(
							styles.itemLabel,
							{ [styles.itemLabelActive] : isMonthlyLimit},
						)}>
							<input 
								className={styles.itemCheckbox} 
								type="checkbox" 
								checked={isMonthlyLimit} 
								onChange={() => setIsMonthlyLimit((monthly) => !monthly)} 
							/>
							 
						</label>
						<div className={styles.itemText}> </div>
					</div>
					<div className={styles.itemRight}>
						<div className={styles.itemLine}></div>
						<ul className={styles.itemLineSublist}>
							<li className={styles.itemLineItem}>
								<button className={clsx(styles.itemLineItemBtn, styles.itemLineItemBtnActive)}>0 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>5 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>10 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>15 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>20 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>25 .</button>
							</li>
							<li className={styles.itemLineItem}>
								<button className={styles.itemLineItemBtn}>1 .</button>
							</li>
						</ul>
					</div>
				</li>
			</ul>
			<button className={styles.btn} disabled></button>
		</div>
				
	);
}

export default LimitSession;